import React, {useState} from 'react';
import { connect } from 'react-redux';
import { addTech } from '../../action/TechAction';
import M from 'materialize-css/dist/js/materialize.min.js';

function AddTechModal({ addTech }) {

  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');

  const onSubmit = () => {
    if(firstName === '' || lastName === ''){
      M.toast({ html: `Please Enter the First Name and Last Name` });
    }
    else{
      console.log({
        firstName,
        lastName
      });
      addTech({
        firstName,
        lastName
      });

      M.toast({ html: `Technician ${firstName} ${lastName} was Added` });

      setFirstName('');
      setLastName('');
    }
  };

  return <div id="add-tech-modal" className="modal">
    <div className="modal-content">
      <h4>New Technician</h4>
      <div className="row">
        <div className="input-field">
        <input type="text" name='firstName' value={firstName} onChange={e => setFirstName(e.target.value)} />
        <label htmlFor="firstName" className="active">
          First Name
        </label>
        </div>
      </div>

      <div className="row">
        <div className="input-field">
        <input type="text" name='lastName' value={lastName} onChange={e => setLastName(e.target.value)} />
        <label htmlFor="lastName" className="active">
          Last Name
        </label>
        </div>
      </div>
    </div>

    <div className="modal-footer">
      <a href="#!" onClick={onSubmit} className="modal-close waves-effects waves-green btn">Enter</a>
    </div>

  </div>;
}


export default connect(null, { addTech })(AddTechModal)